#nav{
    position: fixed;
    z-index: 2;
}

#navBtn{
    position: fixed;
    top: 0.9em;
    right: 4em;

    display: flex;
    align-items: center;
    justify-content: center;

    height: 2.4em;
    width: 2.4em;
    border-radius: 50%;
    background-color: #655057;

    z-index: 3;
}

#navBtn:hover{
    opacity: 0.6;
    transform: translateY(-5px);
    cursor: pointer;
}

#navBtn svg{
    color: #cac3cb;
    height: 1.4em;
}

#navContent{
    display: flex;
    align-items: center;
    justify-content: center;

    height: 4.2em;
    width: 100vw;

    transform: translateY(-4.2em);

    color: #cac3cb;
    background-color: #655057;
}

#navContent.showing{
    transform: translateY(0);
}

#navContent a{
    margin: 0 4em;
    text-align: center;

    font-family: "Source Code Pro", monospace;
}

#navContent a:hover{
    opacity: 0.6;
}
