#bio{
    display: flex;
    flex-direction: row;

    align-items: center;
}

#bio img{
    height: 8em;
    width: 8em;

    border-radius: 50%;
    border: 1px solid #655057;
}

#bio p{
    margin: 0 2em;
    line-height: 1.6em;

    font-size: 1.2em;
    text-align: justify;
}

#skill{
    display: flex;
    flex-direction: row;
}

#skill .card{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    width: 24em;

    margin: 0 2em;
    padding: 2em 0;

    font-weight: lighter;
    color: aliceblue;
    background-color: #655057;
}

#skill .card:hover, #education .singleCard:hover{
    transform: translateY(-10px);
    opacity: 0.9;
}

#skill .card .heading{
    font-size: 1.2em;
}

#skill .br{
    height: 2px;
    width: 2em;

    margin-top: 1em;
    margin-bottom: 4px;
    background-color: #cac3cb;
}

#skill li{
    margin-top: 0.4em;
    font-size: 0.9em;
}

#education{
    display: flex;
    flex-direction: row;
}

#education .singleCard{
    display: flex;
    flex-direction: column;

    width: 20em;
    margin: 0 2em;
    padding: 2em;

    font-weight: lighter;
    color: aliceblue;
    background-color: #655057;
}

#education .singleCard .time{
    font-size: 0.8em;
    margin-bottom: 1em;
}

#education .singleCard .degree{
    font-weight: normal;
    color: white;

    margin-bottom: 2px;
}