/* Global */
*{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;

    transition-duration: 0.6s;
}

a{
    text-decoration: none;
    color: inherit;
}

ul{
    list-style: none;
}

body{
    position: relative;

    height: 100%;
    width: 100%;

    font-family: "Source Sans Pro", sans-serif;
    overflow-x: hidden;
}

/* Bg */
#bg{
    position: fixed;
    height: 100%;
    width: 100%;
    background-image: linear-gradient(to top right, #cac3cb, #d5c8cf);

    z-index: -1;
}

#bgDecoration{
    position: absolute;
    bottom: 0;
    left: 5em;

    height: 20em;
}

/* Main content */
.page{
    display: flex;
    flex-direction: column;
    align-items: center;

    margin: 0 10em;
}

.page .title{
    margin-top: 2em;

    font-family: "Barlow", sans-serif;
    font-size: 3.2em;

    color: #655057;
}

.page .innerTitle{
    margin-top: 2em;
    margin-bottom: 1em;
    font-size: 2em;

    color: #655057;
}

.page .module{
    transform: translateY(100px);
}

.page .module.anime{
    transform: translateY(0);
    animation: easeShow 0.3s ease-in;
}


.page .module.anime2{
    transform: translateY(0);
    animation: easeShow2 0.6s ease-in;
}

/* Animation */
@keyframes show {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes blink {
    20%{
        opacity: 1;
    }
    50%{
        opacity: 0;
    }
    80%{
        opacity: 1;
    }
}

@keyframes blink2 {
    0%{
        opacity: 0;
    }
    60%{
        opacity: 1;
    }
}


@keyframes blink3 {
    20%{
        opacity: 0;
    }
    80%{
        opacity: 1;
    }
}

@keyframes easeShow {
    from{
        opacity: 0;
        transform: translateY(100px);
    }
    to{
        opacity: 1;
        transform: none;
    }
}


@keyframes easeShow2 {
    0%{
        opacity: 0;
        transform: translateY(100px);
    }
    50%{
        opacity: 0;
        transform: translateY(100px);
    }
    to{
        opacity: 1;
        transform: none;
    }
}