@media screen and (max-width: 1200px){

}

@media screen and (max-width: 992px){
    body{
        font-size: 0.9em;
    }
}

@media screen and (max-width: 768px){
    body{
        font-size: 0.8em;
    }

    #coverIcon{
        right: 2em;
    }

    #bgDecoration{
        left: 2em;
    }

    .contactDetail{
        width: 100%;
    }

    .contactDetail a{
        margin: auto;
    }

    #navBtn{
        display: none;
    }

    #navContent{
        transform: translate(0);
    }

    .page{
        margin: 0 4em;
    }

    #bio, #skill, #education{
        flex-direction: column;
    }

    #bio img{
        margin: 1em;
    }

    #bio p{
        margin: 1em;
    }

    #skill .card, #education .singleCard{
        margin-bottom: 2em;
    }

    #works ul{
        flex-direction: column;
        width: auto;
    }

    #works .workCard{
        height: auto;
        width: auto;

        margin: 2em;
    }

    #notes ul{
        width: 100%;
    }
}