#jumpToTop{
    margin: 2em;

    text-align: center;
    cursor: pointer;
}

#jumpToTop svg{
    height: 2em;
}

#jumpToTop:hover svg{
    transform: translateY(-10px);
}

/* Contact */
#contact .innerTitle{
    margin-top: 0;
}

.contactDetail{
    display: flex;
    flex-direction: row;

    list-style: none;

    width: 36em;
    margin-bottom: 3em;
}

.contactDetail a{
    display: block;
    position: relative;

    flex: 1;

    padding: 0.8em;
    margin-left: 1em;
    margin-right: 1em;

    text-align: center;
    font-size: 1.6em;
    color: #655057;
}

.contactDetail .toolTip{
    display: none;
    position: absolute;

    width: 100%;

    left: -0.2em;
    bottom: -2em;

    padding: 0.4em;
    border: 1px solid #655057;

    font-size: 0.6em;
}

.contactDetail a:hover .toolTip{
    display: block;
}

.contactDetail svg{
    height: 1.6em;
    width: 1.6em;
    color: #655057;
}

.contactDetail a:hover svg{
    opacity: 0.8;
    transform: scale(1.1);
}