/* Notes */
#notes ul{
    display: flex;
    flex-direction: column;
    width: 75%;

    margin-top: 4em;
}

.note{
    position: relative;

    padding: 1.2em 2em;

    border-radius: 4px;
    margin-bottom: 2em;

    color: #655057;
    background-color: #C9BCC5;
}

.note:hover{
    background-color: rgba(255, 255, 255, 0.4);
    cursor: pointer;
}

.noteName{
    font-size: 1.4em;

    font-family: 'Source Sans Pro', sans-serif;
}

.noteTag{
    position: absolute;
    right: 2em;
    bottom: 0.9em;

    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.1em;
}

.noteTag svg{
    height: 1em;
    width: 1em;

    margin-right: 0.32em;
    margin-bottom: -4px;
}

.noteDate{
    margin-top: 1em;

    font-family: 'Source Sans Pro', sans-serif;
    font-size: 0.96em;
}

.noteWrapper{
    display: flex;

    align-items: center;
    justify-content: center;
}

.noteContent{
    flex: 1;
    margin: 2em;

    max-width: 80%;

    font-family: "Source Sans Pro", sans-serif;
    color: #655057;
}

.noteContent h3{
    margin-top: 2em;
    margin-bottom: 1em;
    text-align: center;

    font-size: 2em;
}

.noteContent h5{
    margin-top: 1em;
    margin-bottom: 1em;

    font-size: 1.4em;
}

.noteContent a{
    text-decoration: underline;
}

.noteContent a:hover{
    opacity: 0.8;
    text-decoration: none;
}

.noteContent p{
    margin-top: 0.6em;
    margin-bottom: 0.6em;
}

.noteContent ol{
    margin-left: 2em;
    margin-top: 1em;
}

.noteContent li{
    margin-top: 1em;
}

.noteContent pre{
    padding: 2em 4em;
    margin-right: 2em;
    background-color: rgba(0, 0, 0, 0.05);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
}

.noteContent pre, .noteContent code{
    white-space: pre-wrap;
}

.noteContent code{
    line-height: 1.6em;
}

.noteBack{
    position: absolute;

    left: 4em;
    top: 2em;

    z-index: 3;
}

.noteBack svg{
    height: 2em;
    width: 2em;

    color: #655057;
}

.noteBack:hover svg{
    transform: scale(1.1);
}