/* Cover */
#cover{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    position: relative;

    height: 100vh;
    width: 100vw;

    font-family: 'Press Start 2P', cursive !important;
    color: #655057;

    z-index: 2;
    animation: show 0.8s ease-in-out;
}

#coverIcon{
    position: absolute;
    right: 5em;
    bottom: 0;

    height: 10em;
}

#coverTitle{
    margin: 3em;

    font-size: 3em;
}

#discover{
    width: 100%;

    margin: 1em 1em 2em;
    font-size: 1.2em;
    text-align: center;

    z-index: 2;
    cursor: pointer;
}

#discover span{
    margin: 0.32em;
}

#discoverEnd{
    animation: blink 1s infinite ease-in-out;
}

#cover svg{
    height: 1.6em;
    margin: 0.1em;
}

.clickable{
    cursor: pointer;
}

#coverArrow1{
    animation: blink2 1.6s infinite ease-in-out;
}

#coverArrow2{
    animation: blink3 1.6s infinite ease-in-out;
}