#works ul{
    display: flex;
    width: 64em;

    flex-wrap: wrap;
}

.workCard{
    position: relative;

    display: flex;
    align-items: center;
    justify-content: center;

    height: 24em;
    width: 24em;
    margin: 4em;

    cursor: pointer;
}

.workCard img{
    width: 24em;
    height: 24em;
}

.workCard .details{
    display: none;

    position: absolute;
    top: 0;
    left: 0;

    height: 24em;
    width: 24em;

    flex-direction: column;
    align-items: center;
    justify-content: center;

    color: black;
    background-color: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
}

.workCard:hover .details{
    display: flex;
}

.workCard .workName{
    color: #655057;
    font-size: 1.2em;
}

.workCard .workDescription{
    margin: 1em 2em;
}

.workCard .workLinks{
    margin: 2em;
}

.workCard .workLinks a{
    height: 2em;
    margin: 1em;
    padding: 0.6em;

    color: aliceblue;
    background-color: #655057;
}

.workCard .workLinks a:hover{
    opacity: 0.6;
}